.cartListWrapper {
  display: block;
  background-color: #fae5e5;
  top: 0;
  right: 0;
  width: 30%;
  height: 100vh;
  max-height: 100vh;
  position: fixed;
  padding: 10px;
  gap: 10px;
  transition: all 0.8s ease;
  overflow-y: auto;
  z-index: 1000;
}

.cartListClose {
  color: #a93b59;
  font-size: 42px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.closed {
  transform: translateX(150%);
}

.cartListClose:hover {
  color: #a93b58d1;
  scale: 1.3;
}

.cartWrapper {
  position: fixed;
  z-index: 1000;
  bottom: 30px;
  right: 60px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #fff;
  cursor: pointer;
  overflow-y: auto;
}

.FaShoppingCart {
  color: #ef6262;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  opacity: 0.7;
  font-size: 30px;
  transition: 0.3s ease;
}

.cartAmountCounter {
  font-family: sans-serif;
  position: absolute;
  font-weight: 600;
  left: 10px;
  bottom: 5px;
  z-index: 10000;
  font-size: 16px;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ef6262;
  border-radius: 50%;
  background-color: #ffffff;
}

.cartWrapper:hover .FaShoppingCart {
  font-size: 40px;
  cursor: pointer;
  opacity: 1;
}

.cartListItems {
  display: block;
  color: #a93b59;
  background-color: #fff;
  font-size: 24px;
  padding: 5px;
  margin-bottom: 10px;
  border-radius: 10px 10px 10px 10px;
  -webkit-border-radius: 10px 10px 10px 10px;
  -moz-border-radius: 10px 10px 10px 10px;
}

.cartItem {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.cartItemTitle span {
  color: #997777;
}

.cartItemDescr {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.cartImg {
  max-width: 100px;
}

.cartItemTitle {
  font-size: 18px;
  color: #b96a7f;
}

.cartItemPrice {
  font-size: 17px;
  color: #b96a7fb2;
}

.deleteItem {
  cursor: pointer;
  transition: 0.3s ease;
}

.deleteItem:hover {
  scale: 1.3;
  opacity: 0.7;
}

@media screen and (max-width: 1048px) {
  .cartListWrapper {
    width: 50%;
  }
}

@media screen and (max-width: 678px) {
  .cartListWrapper {
    width: 100%;
  }
}
